import React, { useContext, useEffect, useState } from 'react';
import { postJsonDataUrlAfterAPIpushups } from '../../functions/postJsonData';
import PushUpsContext from './state/PushUpsContextState';
// import PushUpsContextState from './state/PushUpsContextState'

import TrainingPlanOverview from './TrainingPlanOverview'
import PersonalPushupPlans from './PersonalPushupPlans';
// import { Link, Route, Switch, HashRouter } from 'react-router-dom';


const PushUpPlans = () => {
  let pushupContext = useContext(PushUpsContext);
  const [currentPlanName, setCurrentPlanName] = useState(null);
  // let ready=true;
  // let ready;

  //display user's current plan name: (get plan's name and display it and cancel button)
  useEffect(() => {
    setCurrentPlanName((pushupContext.user && pushupContext.user.pushupPlans.current) ? pushupContext.user.pushupPlans.current.name : null);
  }, [pushupContext]);

  const commitToPlan = async (planID, planName) => {
    //assuming user exists...
    let updatedUser = await postJsonDataUrlAfterAPIpushups('/commit', { userID: pushupContext.user._id, planID, name: planName });
    pushupContext.updateUser(updatedUser);
  }

  const finishCurrentTrainingPlan = async () => {
    //assuming user and his current plan exists
    let updatedUser = await postJsonDataUrlAfterAPIpushups('/endPlan', {});
    pushupContext.updateUser(updatedUser);


  }

  return (
    <div>
      {/* <Link to='/pushups'>Return</Link> */}
      <h2>Plans</h2>
      {(currentPlanName !== null && currentPlanName !== undefined) ?
        (<div style={{ display: 'flex', justifyContent: 'space-between', backgroundColor: '#cbcbb8', padding: '5px 10px' }}>
          <span>You are currently commited to plan called:<span style={{ fontSize: '1.5rem', fontWeight: 'bold' }}>"{currentPlanName}"</span>.</span>
          <button onClick={finishCurrentTrainingPlan}>End/Archive my current training plan</button>
        </div>)
        : null}
      <TrainingPlanOverview>
        <h3>3 days split - 5x Max reps</h3>
        <p>about the training</p>
        <br />
        {/* <p>Commit to this training for 2 weeks.<button id='trainingPlanID_001' className='btnOpacity' onClick={() => commitToPlan('000000013b3d6905a81d2f88', '3 days split - 5x Max reps')}> Commit</button></p> */}
        <p>Commit to this training for 2 weeks.<button id='trainingPlanID_001' className='btnOpacity' onClick={() => commitToPlan('000000000000000000000001', '3 days split - 5x Max reps')}> Commit (not currently possible as plan not saved in db (and under appropriate id)</button></p>

      </TrainingPlanOverview>



      <PersonalPushupPlans />
    </div>
  );
};

export default PushUpPlans;